import axios from 'axios';
import qs from 'qs';
import { Message } from 'element-ui'
import router from "../router";
import store from "../store";

// transformRequest
function customTransformRequest(data, method) {
  if (method === 'post' || method === 'put' || method === 'patch') {
    // POSTJSON.stringify
    return JSON.stringify(data);
  } else if (method === 'get' || method === 'delete') {
    // GETqs.stringify
    return qs.stringify(data);
  }
  // 
  return data;
}

//request
export function request(config) {
  const instance = axios.create({
    baseURL: 'http://111.229.199.247:8100/',
    // baseURL: 'http://10.174.89.98:8080/',
    timeout: 3000,
    // 
    transformRequest: [function (data) {
      //  data 
      return JSON.stringify(data)
    }],
  })

  instance.defaults.headers.post['Content-Type'] = 'application/json';

  //-------------------------------------//
  instance.interceptors.request.use(config => {
    // 
    // config.data = customTransformRequest(config.data, config.method);
    // tokentoken
    const token = store.state.token;
    if (token) {
      config.headers.Authorization=token;
    }
    // 
    return config;
  }, err => {
    console.log("=>", err);
    return err;
  })

  //-------------------------------------//
  instance.interceptors.response.use(
    res => {
    //
    console.log("=>", res);
    if (res.data.success===false || res.data.data==null) {
      Message({
        message: res.data.message+'',
        type: 'warning'
      });
    }
    return res ? res.data : res;
  },
    err => {
    console.log("=>",err)
    // Message({
    //   message: '',
    //   type: 'info'
    // });
    //
    // if (err.response.status === 401) {
    //   localStorage.removeItem('token');
      // 
      // router.push({
      //   path: '/login'
      // })
    // }
    throw err;
  })

  return instance(config);
}
