<template>
  <div class="article-recommend">
    <MenuCard ref="MenuCard"></MenuCard>
    <!--  -->
    <div class="search-box">
      <el-input placeholder="" v-model="searchText" class="search-input"></el-input>
      <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
    </div>
    <!--  -->
    <div class="category-buttons">
      <span></span>
      <el-button
        v-for="(tag, index) in tags"
        :key="index"
        size="small"
        round
        @click="filterArticles(tag.categoryId)"
      >
        {{ tag.label }}
      </el-button>
    </div>
    <!--  -->
    <div class="featured-articles">
      <h2 class="section-title"></h2>
      <div class="article-list">
        <el-card v-for="(article, index) in articles" :key="index" class="article" shadow="hover"
                 @click.native="itemclick(article)">
          <img :src="article.picture" class="article-image" />
          <div class="article-summary">{{ article.title }}</div>
          <div class="article-details">
            <div class="time">{{ formatDateTime(article.updatedAt) }}</div>
            <div class="read-more">
              <a class="detail-link" @click="itemclick(article)"> >></a>
            </div>
          </div>
        </el-card>
      </div>
      <!--  -->
      <el-pagination class="pagination" :current-page="currentPage" :page-size="pageSize" :total="totalArticles"
            @current-change="handleCurrentChange" background layout="prev, pager, next"  :hide-on-single-page="true">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import MenuCard from '@/components/MenuCard.vue'
import { fetchArticles } from '@/api/api'
export default {
  components: {
    MenuCard
  },
  name: 'ArticleRecommend',
  data() {
    return {
      searchText: '',
      tags: [
        { label: '', categoryId: null },
        { label: '', categoryId: 1 },
        { label: '', categoryId: 2 },
        { label: '', categoryId: 3 },
        { label: '', categoryId: 4 },
        { label: '', categoryId: 5 },
        { label: '', categoryId: 6 },
        { label: '', categoryId: 7 }
      ],
      articles: [{
        title: '',
        picture: require('../assets/article1.jpg'),
        summary: '',
        time: '2024-04-08',
        id: 1
      },
        {
          title: '+',
          picture: require('../assets/article2.jpg'),
          summary: '+',
          time: '2024-01-15'
        },
        {
          title: '',
          picture: require('../assets/article3.jpg'),
          summary: '',
          time: '2024-04-08'
        },
        {
          title: '',
          picture: require('../assets/article4.jpg'),
          summary: '',
          time: '2023-03-16'
        }],
      currentPage: 1,
      pageSize: 4, // 
      totalArticles: 10 // 
    }
  },
  created() {
    const query = {
      current: this.currentPage,
      pageSize: this.pageSize,
    };
    fetchArticles(query).then(res => {
      this.articles = res.data || [];
      // this.totalArticles = res.data.total || 0;
      console.log('res-->', res)
    })
  },
  methods: {
    formatDateTime(dateTimeString) {
      const date = new Date(dateTimeString);
      if (isNaN(date.getTime())) {
        // 
        return dateTimeString;
      }
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, '0'); // getMonth()  0 
      const day = String(date.getDate()).padStart(2, '0');
      const hours = String(date.getHours()).padStart(2, '0');
      const minutes = String(date.getMinutes()).padStart(2, '0');
      return `${year}-${month}-${day} ${hours}:${minutes}`;
    },
    // 
    handleCurrentChange(val) {
      this.currentPage = val;
      fetchArticles({ current: this.currentPage, pageSize: this.pageSize }).then(res => {
        this.article = res.article;
        console.log('handleCurrentChange');
        console.log(res)
        this.articles = res.data || [];
      })
    },
    // 
    itemclick(article) {
      this.$router.push({
        path: `/articleDetail/${article.id}`,
      });
    },
    // 
    search() {
      this.loading = true;
      fetchArticles({ searchText: this.searchText }).then(res => {

        this.articles = res.data;
        // 
        // this.totalArticles = this.articles.length;
        console.log('Search results:', res);
      }).catch(error => {
        console.error('Error fetching articles:', error);
      }).finally(() => {
        this.loading = false;
      });
    },
    // 
    filterArticles(categoryId) {
      this.loading = true;
      fetchArticles({ current: this.currentPage, pageSize: this.pageSize, categoryId: categoryId }).then(res => {
        this.articles = res.data;
        // 
        // this.totalArticles = this.articles.length;
        console.log('Search results:', res);
      }).catch(error => {
        console.error('Error fetching articles:', error);
      }).finally(() => {
        // 
        this.loading = false;
      });
    }
  },
  mounted() {
    // this.fetchArticles(); // 
  }
}
</script>

